<template>
   <div class="max-w-md bg-gray-200 p-4 mt-8 mx-auto rounded">
       <form action="" class="">
           <div class="flex justify-center items-center gap-4 mx-auto" v-if="login">
               <button @click.prevent="changeForm('login')" class="bg-brand py-1.5 px-2 text-white rounded-md">Login</button>
           </div>
           <div class="flex justify-center items-center gap-4 mx-auto" v-if="registration">
               <button @click.prevent="changeForm('login')" class="bg-brand py-1.5 px-2 text-white rounded-md">Registration</button>
           </div>
           <div class="flex flex-col gap-4 mt-4 mx-auto" v-if="registration">
               <Input placeholder="User Name"/>
               <Input placeholder="Email" type="email"/>
               <Input placeholder="Phone" type="number"/>
               <Input placeholder="Passwodrd" type="password"/>
               <Input placeholder="Confirm Password" type="password"/>
           </div>
           <div class="flex flex-col gap-4 mx-auto mt-4" v-if="login">
               <Input placeholder="Email" type="email"/>
               <Input placeholder="Passwodrd" type="password"/>
           </div>
           <div v-if="login" class="text-left mt-4">
               <span class="w-full text-sky-500 p-2">If your are not registrated ? Please click </span>
                <a @click.prevent="changeForm('registration')" class="underline cursor-pointer hover:text-blue-700 font-semibold">Registration</a>
           </div>
           <div v-if="registration" class="text-left mt-4">
               <span class="w-full text-sky-500 p-2">You are already registrated ? Please click</span>
                <a  @click.prevent="changeForm('login')" class="underline cursor-pointer hover:text-blue-700 font-semibold">Login</a>
           </div>
           <div class="mt-4">
               <button type="submit" class="bg-blue-500 py-1.5 px-2 text-white rounded-md">Submit</button>                 
           </div>
       </form>
   </div>
</template>

<script>
import Input from '../../components/input-new.vue'
import Button from '../../components/btn.vue'
export default {
   components:{
       Input,
       Button
   },
   data() {
    return {
      login: true,
      registration: false
    }
  },
   methods:{
       changeForm(data){
           if(data === 'registration')
           {
               this.registration = true;
               this.login = false
           }
           if(data === 'login')
           {
               this.registration = false;
               this.login = true
           }
       }


   }
}
</script>